import L from 'leaflet';
import '../../core/Base';
import FetchRequest from "../../util/FetchRequest";
import FeatureAttr from "./FeatureAttr";

export let FeatureService = L.Evented.extend({
    options: {
        url: null
    },
    initialize: function (featureGroup, options) {
        this.featureGroup = featureGroup;
        L.setOptions(this, options);
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#setUrl
     * @param {string} url - 
     */
    setUrl: function(url){
        this.options.url = url;
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#info
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    info: function(successFun, failFun){
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/info';
        FetchRequest.get(url).then(function(result){
            if(result.status === "ok"){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#infoById
     * @param {string} layerID -id
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    infoById: function(layerID, successFun, failFun){
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/'+ layerID + '/info';
        FetchRequest.get(url).then(function(result){
            if(result.status === "ok"){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * &
     * @method L.geoway.service.FeatureService#query
     * @param {Object} params - 
     * @param {string} params.layerId - id
     * @param {string} [params.geometry] - ,wkt
     * @param {string} [params.spatialOper=Intersects] - IntersectsTouchesCrossesWithinContains
     * @param {string} [params.where] - name like %%
     * @param {string} [params.orderBy] - (,)
     * @param {boolean} [params.getGeometry=true]- true
     * @param {number} [params.pageNum=0] - 0
     * @param {number} [params.pageSize=10] - 10
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    query: function (params, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        if(!params&&!params.layerId){
            return false;
        }
        url = url + '/' + params.layerId + '/query';
        let data = {};
        for(let key in params){
           if(key === 'layerId'){
               continue;
           }
           data[key] = params[key];
        }
        FetchRequest.get(url,data).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * &
     * @method L.geoway.service.FeatureService#queryMulti
     * @param {Object} params -
     * @param {string} [params.geometry] -,wkt
     * @param {string} [params.spatialOper=Intersects] -IntersectsTouchesCrossesWithinContains
     * @param {boolean} [params.getGeometry=TRUE] -
     * @param {Object[]} params.layers -
     * @param {string} params.layers[].layerid -ID
     * @param {string} [params.layers[].where] -
     * @param {string} [params.layers[].orderBy] -
     * @param {number} [params.layers[].pageNum=0] -
     * @param {number} [params.layers[].pageSize=10] -
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    queryMulti: function (params, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/querymulti';
        params.layers = JSON.stringify(params.layers);
        FetchRequest.get(url,params).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * identify
     * @method L.geoway.service.FeatureService#identify
     * @param {number} x -ix
     * @param {number} y -iy
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    identify: function (data, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/identify';
        FetchRequest.get(url, data).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#detail
     * @param {string} layerID - ID
     * @param {string} fids - objectid,
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    detail: function (layerID, fids, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/'+ layerID + '/detail';
        FetchRequest.get(url,{fids}).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#addFeatures
     * @param {string} layerID - ID
     * @param {Object} data - feature json
     * @param {Function} successFun -
     * @param {Function} failFun -
     *  data= [{
       geometry:"POLYGON((116.68750000000011 28.125000000000057,116.68750000000011 28.166666667000072,116.75000000000011 28.166666667000072,116.75000000000011 28.125000000000057,116.68750000000011 28.125000000000057))",
       attributes:
       {F_FRAME:"H50G093044_v",
        NEW_MAPNO:"H50G093044_v",
        SHAPE_Leng:2000,
        F_RENDER:0
        }}];
     */
    addFeatures: function (layerID, data, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/'+ layerID + '/addFeatures';
        FetchRequest.post(url,data).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#updateFeatures
     * @param {string} layerID - ID
     * @param {Object} data
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    updateFeatures: function (layerID, data, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/'+ layerID + '/updateFeatures';
        FetchRequest.post(url,data).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e.message);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#deleteFeatures
     * @param {string} layerID - ID
     * @param {string} objectIds - 
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    deleteFeatures: function (layerID, objectIds, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/'+ layerID + '/deleteFeatures' + '?objectIds=' + objectIds;
        FetchRequest.post(url).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#bufferAnalysis
     * @param {string} layerID - ID
     * @param {Object} data - json
     * @param {number} data.fId -id
     * @param {number} data.distance - m
     * @param {number} data.type=1 - 1231
     * @param {number} data.accuracy=8 - 8
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    bufferAnalysis: function (layerID, data, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/'+ layerID + '/bufferAnalysis';
        FetchRequest.post(url, data).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#overlayAnalysis
     * @param {Object} data - json
     * @param {number} data.sourceLayer -id
     * @param {number} data.targetLayer -id
     * @param {Function} successFun -
     * @param {Function} failFun -
     */
    overlayAnalysis: function (data, successFun, failFun) {
        var url = this.options.url;
        if(!url) {return false;}
        url = url + '/overlayAnalysis';
        FetchRequest.post(url, data).then(function (result) {
            if(result.status === 'ok'){
                successFun(result);
            }else{
                if(failFun){
                    failFun(result);
                }
            }
        }).catch(function (e) {
            if(failFun){
                failFun(e);
            }
        });
    },
    /**
     * 
     * @method L.geoway.service.FeatureService#showAttr
     * @param {Object} options -
     * @param {boolean} [options.isEdit] -
     * @param {number} options.layerId -id
     * @param {Object} options.feature -
     * @param {number} options.fId -id
     * @param {Object} [options.position] -
     * @param {number} [options.width] -
     * @param {number} [options.height] -
     * @param {Object} attributes -
     */
    showAttr: function(options, attributes){
        let featureAttr = new FeatureAttr(this, options);
        featureAttr.render(attributes);
    }
});

export let featureService = function (featureGroup,options) {
    return new FeatureService(featureGroup,options);
}

L.geoway.service.featureService = featureService;
L.geoway.service.FeatureService = FeatureService;

