import snappy from 'snappyjs';

class VarintReader {
    constructor(vectorVarintBuffer, hd) {
        this.hd = hd;
        this.intLength = 4;
        this.vectorVarintBuffer = vectorVarintBuffer;
        this.geometyTypeMap = new Map();
        this.proMap = new Map();
        this.varintMap = new Map();
        this.layerMap = new Map();
        this.offsetMap = new Map();

        this.proMapOld = new Map();
        this.offsetMapOld = new Map();

        this.addVector(vectorVarintBuffer);
    }

    /**
     * 
     * @param vectorVarintBuffer varintBuffer
     */
    addVector(vectorVarintBuffer) {
        //geometry
        let offset = this.hd;
        let typeLength = vectorVarintBuffer.readInt32BE(offset);
        offset = offset + this.intLength;
        //vectorgeometry
        let type = vectorVarintBuffer.toString('utf8', offset, offset + typeLength);
        offset = offset + typeLength;
        if (type != 'vector') {
            console.error('');
            return;
        }
        this._readVector(vectorVarintBuffer, offset);
    }

    /**
     * 
     * @param layerName 
     * @param layerVarintBuffer varintBuffer
     */
    addLayer(layerName, layerVarintBuffer) {
        //geometry
        let offset = this.hd;
        let typeLength = this.vectorVarintBuffer.readInt32BE(offset);
        offset = offset + this.intLength;
        //vectorgeometry
        let type = this.vectorVarintBuffer.toString('utf8', offset, offset + typeLength);
        offset = offset + typeLength;
        if (type != 'vector') {
            this._readLayer(layerName, this.vectorVarintBuffer);
        } else {
            console.error('');
        }
    }

    /**
     * varintBuffer
     */
    getBuffer() {
        let data = [];
        let idx = [];
        let keyBytes;
        let layerBuf;
        for (let key of this.layerMap.keys()) {
            layerBuf = this._getLayerBuffer(key);
            keyBytes = this._stringToByte(key);
            idx.push(...this._intToBytes(keyBytes.length));
            idx.push(...keyBytes);
            idx.push(...this._intToBytes(layerBuf.length));
            data.push(layerBuf);
        }

        let indexBuf = Buffer.from(idx);
        let dataBuf = Buffer.concat(data);

        let layerHeader = [];
        let typeByte = this._stringToByte('vector');
        layerHeader.push(...this._getHeaderArray());
        layerHeader.push(...this._intToBytes(typeByte.length));
        layerHeader.push(...typeByte);
        layerHeader.push(...this._intToBytes(indexBuf.length));

        let layerHeaderBuf = Buffer.from(layerHeader);

        return Buffer.concat([layerHeaderBuf, indexBuf, dataBuf]);
    }

    /**
     * //
     * @returns {[]}
     * @private
     */
    _getHeaderArray() {
        let harr = [];
        for (let i = 0; i < this.hd; i++) {
            harr[i] = this._getRandomNum();
        }
        return harr;
    }

    /**
     * layer
     * @param layerName
     * @returns {Buffer}
     * @private
     */
    _getLayerBuffer(layerName) {
        let layerBuffer = [];
        let gtype = this._stringToByte(this.geometyTypeMap.get(layerName));
        let gTypeLength = this._intToBytes(gtype.length);
        let json = this.proMapOld.get(layerName);
        let jsonLength = this._intToBytes(json.length);
        let offset = this.offsetMapOld.get(layerName);
        let offsetLength = Buffer.from(this._intToBytes(offset.length));
        let data = this.varintMap.get(layerName);

        layerBuffer.push(...this._getHeaderArray());
        layerBuffer.push(...gTypeLength);
        layerBuffer.push(...gtype);
        layerBuffer.push(...jsonLength);

        return Buffer.concat([Buffer.from(layerBuffer), json, offsetLength, offset, data]);
    }

    /**
     * 
     * @returns {number}
     * @private
     */
    _getRandomNum() {
        var Range = 80;
        var Rand = Math.random();
        return 50 + Math.round(Rand * Range);
    }

    /**
     * byte
     * @param str
     * @returns {[]}
     * @private
     */
    _stringToByte(str) {
        let bytes = [];
        let buffer = Buffer.from(str, 'utf8');
        for (let i = 0; i < buffer.length; i++) {
            bytes.push(buffer[i]);
        }

        return bytes;
    }

    /**
     * byte
     * @param bytes
     * @returns {string}
     * @private
     */
    _byteToString(bytes) {
        return Buffer.from(bytes).toString();
    }

    /**
     * byte
     * @param num
     * @returns {number[]}
     * @private
     */
    _intToBytes(num) {
        return [(num >> 24) & 0x00ff, (num >> 16) & 0x00ff, (num >> 8) & 0x00ff, num & 0x00ff];
    }

    /**
     * byte
     * @param bytes
     * @returns {number}
     * @private
     */
    _bytesToInt(bytes) {
        return (bytes[0] << 24) | (bytes[1] << 16) | (bytes[2] << 8) | bytes[3];
    }

    /**
     * 
     * @returns {[1 2 ...]}
     */
    getAllLayerNames() {
        let layerNames = [];
        for (let value of this.layerMap) {
            layerNames.push(value[0]);
        }
        return layerNames;
    }

    /**
     * 
     * @param layerName
     * @returns {any}
     */
    getGeometryType(layerName) {
        return this.geometyTypeMap.get(layerName);
    }

    /**
     * 
     * @param layerName 
     * @returns {}
     */
    getLayerPro(layerName) {
        let jsonBuffer = this.proMap.get(layerName);
        // let obj = null;
        // if (Buffer.isBuffer(jsonBuffer)) {
        //     let jsonString = snappy.uncompress(jsonBuffer).toString();
        //     obj = JSON.parse(jsonString);
        //     this.proMap.set(layerName, obj);
        // } else {
        //     obj = this.proMap.get(layerName);
        // }
        //return obj;
        return snappy.uncompress(jsonBuffer);
    }

    /**
     * 
     * @param layerName 
     * @returns {[11 ,22 ,....]}
     */
    getOffsetArray(layerName) {
        return this.offsetMap.get(layerName);
    }

    /**
     * 
     * @param layerName 
     * @param proIndex 
     * @returns {[]}
     */
    getOffsetByIndex(layerName, proIndex) {
        let all = this.offsetMap.get(layerName);

        let arr = [];
        arr.push(all[proIndex * 2]);
        arr.push(all[proIndex * 2 + 1]);

        return arr;
    }

    /**
     * 
     * @param layerName 
     * @returns {Buffer}
     */
    getLayerCoordinate(layerName) {
        return this.varintMap.get(layerName);
    }

    /**
     * 
     * @param layerName 
     * @returns {any[  varint]}
     */
    getLayerFeature(layerName) {
        var allArr = ["", ""];
        var proArr = this.proMap.get(layerName);
        var offsetArr = this.offsetMap.get(layerName);
        //allArr[1] = offsetArr;
        var varintArr = this.varintMap.get(layerName);
        allArr[1] = varintArr;
        if (proArr.length > 0) {
            allArr[0] = snappy.uncompress(proArr);
        }
        if (offsetArr.length > 0) {
            ///allArr[1] = snappy.uncompress(offsetArr);
        }
        if (varintArr.length > 0) {
            //allArr[1] = snappy.uncompress(varintArr);
        }
        return allArr;
    }

    /**
     * 
     * @param layerName 
     * @param precision 
     * @returns {[[1],[2],...]}
     */
    getAllCoordinates(layerName, precision) {
        let ofs = this.getOffsetArray(layerName);
        let buffer = this.varintMap.get(layerName);
        let coorArray = [];
        let offset;
        let length;
        let bf;
        for (let i = 0; i < ofs.length / 2; i++) {
            offset = ofs[i * 2];
            length = ofs[i * 2 + 1];
            bf = buffer.slice(offset, offset + length);
            coorArray.push(this._bufferToDoubleArray(bf, precision));
        }
        return coorArray;
    }

    /**
     * 
     * @param layerName 
     * @param proIndex 
     * @param precision 
     * @returns {[]}
     */
    getCoordinatesByIndex(layerName, proIndex, precision) {
        let ofs = this.getOffsetByIndex(layerName, proIndex);
        let buffer = this.varintMap.get(layerName);
        let offset = ofs[0];
        let length = ofs[1];
        let bf = buffer.slice(offset, offset + length);
        return this._bufferToDoubleArray(bf, precision);
    }

    /**
     * Buffer
     * @param layerName 
     * @param buffer varintBuffer
     * @private
     */
    _readLayer(layerName, buffer) {
        if (buffer.length == 0) {
            return;
        }

        let offset = this.hd;
        //
        let typeLength = buffer.readInt32BE(offset);
        offset = offset + this.intLength;
        let geometrytype = buffer.toString('utf8', offset, offset + typeLength);
        this.geometyTypeMap.set(layerName, geometrytype);
        //json
        offset = offset + typeLength;
        let jsonLength = buffer.readInt32BE(offset);
        offset = offset + this.intLength;
        let jsonBuffer = buffer.slice(offset, offset + jsonLength);
        this.proMap.set(layerName, jsonBuffer);
        this.proMapOld.set(layerName, jsonBuffer);
        //
        offset = offset + jsonLength;
        let offsetLength = buffer.readInt32BE(offset);
        offset = offset + this.intLength;
        let offsetBuffer = buffer.slice(offset, offset + offsetLength);
        let indexArray = this._varintToIntArray(offsetBuffer);
        this.offsetMap.set(layerName, indexArray);
        this.offsetMapOld.set(layerName, offsetBuffer);
        //
        offset = offset + offsetLength;
        let varintBuf = buffer.slice(offset);
        this.varintMap.set(layerName, varintBuf);
    }

    /**
     * 
     * @param buffer varintBuffer
     * @param offset 
     * @private
     */
    _readVector(buffer, offset) {
        //
        let indexLength = buffer.readInt32BE(offset);
        //index
        offset = offset + this.intLength;
        let indexBuffer = buffer.slice(offset, offset + indexLength);
        //
        offset = offset + indexLength;
        let dataBuffer = buffer.slice(offset, this.vectorVarintBuffer.length);

        let layerNameLength = 0;
        let layerName;
        let dataLength;
        let dataBegin = 0;
        let begin = 0;
        let end = 0;
        let tmpBuffer;
        for (let i = 0; i < indexBuffer.length;) {
            begin = i;
            layerNameLength = indexBuffer.readInt32BE(begin);
            begin = begin + this.intLength;
            end = begin + layerNameLength;
            layerName = indexBuffer.toString('utf8', begin, end);
            begin = end;
            dataLength = indexBuffer.readInt32BE(begin);
            begin = begin + this.intLength;
            //layer
            tmpBuffer = dataBuffer.slice(dataBegin, dataBegin + dataLength);
            dataBegin = dataBegin + dataLength;
            this.layerMap.set(layerName, tmpBuffer);
            this._readLayer(layerName, tmpBuffer);
            i = begin;
        }
    }

    /**
     * varint
     * @param buffer varint
     * @param precision 
     * @returns {[]}
     * @private
     */
    _bufferToDoubleArray(buffer, precision) {
        let varintCode = [];
        let high;
        let bf_index = 0;
        let nums = [];
        let oldX = 0;
        let oldY = 0;
        let x;
        let y;
        let isX = true;
        for (let i = 0; i < buffer.length; i++) {
            //buffer.length
            high = 0x00000000 | ((buffer[i] >> 7) & 0x01);
            if (high == 0) {
                if (bf_index > 0 && bf_index <= 5) {
                    if (isX) {
                        x = this._varintToInt(varintCode) + oldX;
                        nums.push(x / precision);
                        isX = false;
                        oldX = x;
                    } else {
                        y = this._varintToInt(varintCode) + oldY;
                        nums.push(y / precision);
                        isX = true;
                        oldY = y;
                    }
                    bf_index = 0;
                    varintCode = [];
                } else if (bf_index > 5) {
                    //bf_index > 5
                    oldX = -99999;
                    oldY = -99999;
                    bf_index = 0;
                    varintCode = [];
                }
            }

            varintCode.push(buffer[i]);
            bf_index++;
        }
        return nums;
    }

    /**
     * varint
     * @param buffer
     * @returns {[]}
     * @private
     */
    _varintToIntArray(buffer) {
        let varintCode = [];
        let high;
        let bf_index = 0;
        let nums = [];
        for (let i = 0; i < buffer.length; i++) {
            high = 0x00000000 | ((buffer[i] >> 7) & 0x01);
            if (high == 0) {
                if (bf_index > 0 && bf_index <= 5) {
                    nums.push(this._varintToInt(varintCode));
                    varintCode = [];
                    bf_index = 0;
                }
            }
            varintCode.push(buffer[i]);
            bf_index++;
        }
        nums.push(this._varintToInt(varintCode));
        return nums;
    }

    /**
     * varint
     * @param buffer varint
     * @returns {number}
     * @private
     */
    _varintToInt(buffer) {
        let intBuffer = [];
        let highBuffer = [];
        let j = 3;
        let offset = 0;
        for (let i = buffer.length - 1; i >= 0; i--) {
            if (i - 1 >= 0) {
                highBuffer[0] = buffer[i - 1] << (7 - offset);
            } else {
                highBuffer[0] = 0;
            }
            if (j == -1) {
                break;
            }
            intBuffer[j] = ((buffer[i] & 0x7f) >> offset) | highBuffer[0];
            j--;
            offset++;
        }

        let t =
            ((intBuffer[0] << 24) & 0xff000000) |
            ((intBuffer[1] << 16) & 0x00ff0000) |
            ((intBuffer[2] << 8) & 0x0000ff00) |
            (intBuffer[3] & 0x000000ff);
        t = (t >> 1) ^ -(t & 1);
        return t;
    }

    /**
     * varint
     * @param num
     * @returns {[]|number[]}
     * @private
     */
    _intToVarint(num) {
        if (num == 0) {
            return [0];
        }

        num = this._zigZagEncoding(num);
        let buffer = [];
        let now;
        let high = 0;
        for (let i = 5; i > 0; i--) {
            now = (num >> ((i - 1) * 7)) & 0x7f;
            if (high != 0) {
                now = now | 0x80;
            }
            if (now != 0) {
                buffer.push(now);
            }
            high = now;
        }
        return buffer;
    }

    /**
     * 
     * @param num
     * @returns {number}
     * @private
     */
    _zigZagEncoding(num) {
        return (num >> 31) ^ (num << 1);
    }
}
export default VarintReader;