import L from "leaflet";
import "../core/Base";
class FilterLayer {
    constructor() {
        //
        this.id = null;
        //
        this.filters = {};
        //id
        this.idFilter = null;
        //,filtersfilterStr
        this.filterStr = null;
        //
        this.display = true;
        //,null {"color":"%23f00fff","opacity":0.9} %23
        this.color = null;
    }

    /**
     * 
     * Parameters :
     * key -  Q_fcode_S_EQfcodevalue
     * value - 2101010500
     */
    addFilterField(key, value) {
        this.filters[key] = value;
    }

    /**
     * 
     * Parameters :
     * key
     */
    removeFilterField(key) {
        delete this.filters[key];
    }
}

L.geoway.FilterLayer = FilterLayer;

export default FilterLayer;