import L from 'leaflet';

let RoutePlan = L.Class.extend({
    options: {
        plan: null
    },
    initialize: function(options){
        L.setOptions(this, options);
    },
    /**
     * 
     * @method L.RoutePlan#getDistance
     * @return {string} len - 
     */
    getDistance: function(){
        var plan = this.options.plan;
        if(plan){
            return parseInt(plan.distance / 1000) + ""
        }
        return null;
    },
    /**
     * 
     * @method L.RoutePlan#getDistance
     * @return {string} time - 
     */
    getDuration: function(){
        var plan = this.options.plan;
        if(plan){
            return this._formatMinute(plan.duration);
        }
        return null;
    },
    /**
     * 
     * @method L.RoutePlan#getNumSteps
     * @return {number} num - 
     */
    getNumSteps: function(){
        var plan = this.options.plan;
        if(plan){
            return plan.steps.length;
        }
        return null;
    },
    /**
     * 
     * @method L.RoutePlan#getStep
     * @param  {number} num - 
     * @return {object} step - 
     */
    getStep: function(num){
        var plan = this.options.plan;
        if(plan){
            var steps = plan.steps;
            return steps[num];
        }
        return null;
    },
    _formatMinute:function(value) {
        var theTime = parseInt(value);
        var theTime1 = 0;
        if(theTime > 60) {
            theTime1 = parseInt(theTime/60);
            theTime = parseInt(theTime%60);
        }
        var result = 0;
        if(theTime1 > 0) {
            result = ""+parseInt(theTime1)+"";
        }
        result = result + theTime + "";
        return result;
    }
});

export default RoutePlan;
